// pages/shifts.js
import { useState, useEffect } from "react";
import Layout from "../components/Layout";
import api from "../utils/api";
import toast from "react-hot-toast";

export default function ShiftsPage() {
  const [loading, setLoading] = useState(true);
  const [currentShift, setCurrentShift] = useState(null);
  const [shifts, setShifts] = useState([]);

  const userId = 1; // ŸÑÿßÿ≠ŸÇÿßŸã ÿßÿ¨ŸÑÿ®Ÿáÿß ŸÖŸÜ AuthContext

  // -----------------------------------------------------
  // üî• ÿ™ÿ≠ŸÖŸäŸÑ ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑÿ¥ŸÅÿ™ ŸÖŸÜ API
  // -----------------------------------------------------
  const loadData = async () => {
    try {
      setLoading(true);

      const [cur, list] = await Promise.all([
        api.get("/shifts/current"),
        api.get("/shifts"),
      ]);

      setCurrentShift(cur.data || null);
      setShifts(list.data || []);
    } catch (err) {
      console.error("loadData error:", err);
      toast.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑÿ¥ŸÅÿ™");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadData();
  }, []);

  // -----------------------------------------------------
  // üü¢ ÿ®ÿØÿ° ÿ¥ŸÅÿ™ ÿ¨ÿØŸäÿØ
  // -----------------------------------------------------
  const startShift = async () => {
    try {
      await api.post("/shifts/start", { userId });
      toast.success("‚úÖ ÿ™ŸÖ ÿ®ÿØÿ° ÿßŸÑÿ¥ŸÅÿ™");
      await loadData();
    } catch (err) {
      console.error(err);
      toast.error(err.response?.data?.message || "ÿ™ÿπÿ∞ÿ± ÿ®ÿØÿ° ÿßŸÑÿ¥ŸÅÿ™");
    }
  };

  // -----------------------------------------------------
  // üî¥ ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑÿ¥ŸÅÿ™
  // -----------------------------------------------------
  const closeShift = async () => {
    try {
      await api.post("/shifts/close", { userId });
      toast.success("üîí ÿ™ŸÖ ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑÿ¥ŸÅÿ™");
      await loadData();
    } catch (err) {
      console.error(err);
      toast.error(err.response?.data?.message || "ÿ™ÿπÿ∞ÿ± ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑÿ¥ŸÅÿ™");
    }
  };

  // -----------------------------------------------------
  // üé® Ÿàÿßÿ¨Ÿáÿ© ÿßŸÑÿπÿ±ÿ∂
  // -----------------------------------------------------
  return (
    <Layout title="ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿ¥ŸÅÿ™ÿßÿ™">
      <div className="p-5 space-y-6" dir="rtl">
        <h1 className="text-2xl font-bold text-slate-800">üïí ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿ¥ŸÅÿ™</h1>

        {/* ÿßŸÑÿ¥ŸÅÿ™ ÿßŸÑÿ≠ÿßŸÑŸä */}
        <div className="p-4 bg-white border shadow-sm rounded-xl">
          <h2 className="mb-3 text-lg font-semibold text-slate-700">
            ÿßŸÑÿ¥ŸÅÿ™ ÿßŸÑÿ≠ÿßŸÑŸä
          </h2>

          {loading ? (
            <p className="text-gray-500">ÿ¨ÿßÿ±Ÿç ÿßŸÑÿ™ÿ≠ŸÖŸäŸÑ‚Ä¶</p>
          ) : currentShift ? (
            <div className="space-y-2 text-sm">
              <p><strong>ŸàŸÇÿ™ ÿßŸÑŸÅÿ™ÿ≠:</strong> {currentShift.open_time}</p>
              <p><strong>ÿ®Ÿàÿßÿ≥ÿ∑ÿ©:</strong> ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ #{currentShift.opened_by}</p>
              <p><strong>ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™:</strong> {currentShift.total_sales} ÿ±.ÿ≥</p>
              <p><strong>ŸÅŸàÿßÿ™Ÿäÿ±:</strong> {currentShift.invoices_count}</p>
              <p><strong>ŸÜŸÇÿØ:</strong> {currentShift.total_cash}</p>
              <p><strong>ÿ®ÿ∑ÿßŸÇÿ©:</strong> {currentShift.total_card}</p>
              <p><strong>ŸÖÿ≠ŸÅÿ∏ÿ©:</strong> {currentShift.total_wallet}</p>

              <button
                onClick={closeShift}
                className="px-4 py-2 mt-3 text-white bg-red-600 rounded-lg hover:bg-red-700"
              >
                üî¥ ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑÿ¥ŸÅÿ™
              </button>
            </div>
          ) : (
            <div>
              <p className="mb-3 text-gray-500">ŸÑÿß ŸäŸàÿ¨ÿØ ÿ¥ŸÅÿ™ ŸÖŸÅÿ™Ÿàÿ≠ ÿ≠ÿßŸÑŸäÿßŸã.</p>
              <button
                onClick={startShift}
                className="px-4 py-2 text-white rounded-lg bg-emerald-600 hover:bg-emerald-700"
              >
                üü¢ ÿ®ÿØÿ° ÿ¥ŸÅÿ™ ÿ¨ÿØŸäÿØ
              </button>
            </div>
          )}
        </div>

        {/* ÿßŸÑÿ≥ÿ¨ŸÑ ÿßŸÑŸÉÿßŸÖŸÑ */}
        <div className="p-4 bg-white border shadow-sm rounded-xl">
          <h2 className="mb-3 text-lg font-semibold text-slate-700">
            üìù ÿßŸÑÿ≥ÿ¨ŸÑ ÿßŸÑŸÉÿßŸÖŸÑ ŸÑŸÑÿ¥ŸÅÿ™ÿßÿ™
          </h2>

          {loading ? (
            <p className="text-gray-500">ÿ¨ÿßÿ±Ÿç ÿßŸÑÿ™ÿ≠ŸÖŸäŸÑ‚Ä¶</p>
          ) : (
            <table className="w-full text-sm border">
              <thead className="bg-slate-100 text-slate-600">
                <tr>
                  <th className="p-2 border">ÿ±ŸÇŸÖ ÿßŸÑÿ¥ŸÅÿ™</th>
                  <th className="p-2 border">ŸÅÿ™ÿ≠</th>
                  <th className="p-2 border">ÿ•ÿ∫ŸÑÿßŸÇ</th>
                  <th className="p-2 border">ÿßŸÑÿ≠ÿßŸÑÿ©</th>
                  <th className="p-2 border">ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™</th>
                </tr>
              </thead>
              <tbody>
                {shifts.map((s) => (
                  <tr key={s.id} className="border-t hover:bg-slate-50">
                    <td className="p-2 border">{s.id}</td>
                    <td className="p-2 border">{s.open_time}</td>
                    <td className="p-2 border">{s.close_time || "---"}</td>
                    <td className="p-2 border">
                      {s.status === "open" ? "üîµ ŸÖŸÅÿ™Ÿàÿ≠" : "‚ö´ ŸÖÿ∫ŸÑŸÇ"}
                    </td>
                    <td className="p-2 border">{s.total_sales} ÿ±.ÿ≥</td>
                  </tr>
                ))}

                {!shifts.length && (
                  <tr>
                    <td colSpan={5} className="py-4 text-center text-gray-500">
                      ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ¥ŸÅÿ™ÿßÿ™ ŸÖÿ≥ÿ¨ŸÑÿ©.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </Layout>
  );
}





















